<template>
	<view class="box">
		<view class="mybox">

			<view class="pay-li" v-for="(item,k) in withdrawList" :key="k" @tap="chooseWithdraw(item.tradeManner)">
				<view class="left">
					<image lazy-load class="img" :src="'../../static/images/'+item.img+'.png'" mode="aspectFill"></image>
					<text class="text">
						{{item.title}}
					</text>
				</view>
				<view class="iconfont iconlujing" :class="{'iconxuanzhong':tradeManner==item.tradeManner}"></view>
			</view>
		</view>
		<form @submit="submit">
			<view class="mybox">
				<input class="input" value="" name="realName" type="text" placeholder="请输入姓名">
				<input v-if="tradeManner!='2'" class="input" value="" name="account" type="text" placeholder="请输入账号">
				<input class="input" value="" name="mobileNo" type="text" placeholder="请输入手机号码" maxlength="11">
			</view>
			<view class="mybox">
				<view class="title-line">
					提现金额 <text class="font-small">（收取{{handlingFee}}%手续费）</text>
				</view>
				<input name="tradeAmount" v-model="tradeAmount" class="input" type="text" :placeholder="'输入金额(最少提现'+minWithdraw+'元)'">
				<view class="tips-box">
					<view class="tips">当前最多可提取{{allMoney | formatNum}}元</view>
					<view class="allin" @tap="tradeAmount=allMoney">全部提现</view>
				</view>
			</view>
			<button class="no-btn confirm-btn" formType="submit" :loading="loading">我要提现</button>
		</form>
	</view>
</template>

<script>
	// 门店最少提现金额   WITHDRAWAL_LEAST_MD_VALUE
	// 佣金最少提现金额   WITHDRAWAL_LEAST_CASH_VALUE
	export default {
		data() {
			return {
				loading: false,
				allMoney: 0,
				withdrawalType: "", //提现类型
				tradeAmount: "",
				realName: "", // 姓名
				account: "", // 账号
				mobileNo: "", // 手机号
				withdrawList: [{
						title: "支付宝提现",
						img: "alipay",
						tradeManner: 1
					},
					{
						title: "微信提现",
						img: "wechat",
						tradeManner: 2
					}
				],
				tradeManner: "", //“1”:支付宝提现  “2”：微信提现  “3”.银行卡提现
				feeMap: ["", "", "", "WITHDRAWAL_YJ_CAPITAL", "", "WITHDRAWAL_MD_CAPITAL"],
				minWithMap: ["", "", "", "WITHDRAWAL_LEAST_CASH_VALUE", "", "WITHDRAWAL_LEAST_MD_VALUE"],
				handlingFee: 0,
				minWithdraw: 0
			}
		},
		onLoad(option) {
			this.allMoney = option.money;
			this.withdrawalType = option.type;
			let label1 = this.feeMap[option.type]
			let label2 = this.minWithMap[option.type]
			this.loadFee(label1, 'handlingFee'); //手续费
			this.loadFee(label2, 'minWithdraw'); //最少提现金额
			this.tradeManner = this.withdrawList[0].tradeManner;
		},
		methods: {
			chooseWithdraw(id) {
				this.tradeManner = id
			},
			submit(e) {
				let self = this;
				if (this.loading) return;
				var value = e.detail.value
				this.realName = value.realName
				this.account = value.account
				this.mobileNo = value.mobileNo
				this.tradeAmount = value.tradeAmount
				if (!this.realName) {
					this.$box.toast('请输入姓名', 'none');
					return
				}
				if (!this.account && this.tradeManner != 2) {
					this.$box.toast('请输入账号', 'none');
					return
				}
				let phonereg = /^1[3456789]\d{9}$/
				if (!(phonereg.test(this.mobileNo))) {
					this.$box.toast('请填写正确的手机号码', 'none');
					return;
				}
				if (this.tradeAmount < this.minWithdraw) {
					this.$box.toast('最少提现金额' + this.minWithdraw, 'none');
					return
				}
				if (this.tradeAmount > this.allMoney) {
					this.$box.toast('提现金额超过账户余额', 'none');
					return
				}
				self.loading = true;
				if (this.tradeManner != 2) {
					uni.showModal({
						title: '温馨提示',
						content: '请确认提现到账号' + self.account + "?",
						success: function(res) {
							if (res.confirm) {
								self.withdraw()
							} else if (res.cancel) {
								self.loading = false;
							}
						}
					});
				} else {
					self.withdraw()
				}
			},
			withdraw() {
				this.$api.post('/o2oCustomerCash/withdrawal', {
					userSeqId: this.$common.getLocalSync('customerSeqId'),
					tradeManner: this.tradeManner,
					realName: this.realName,
					account: this.account,
					mobileNo: this.mobileNo,
					tradeAmount: this.tradeAmount,
					withdrawalType: this.withdrawalType
				}).then(res => {
					this.loading = false;
					if (res.code == 10012) {
						uni.navigateTo({
							url: '/pages/login/login_wechat'
						})
					} else {
						this.$box.confirm(res.msg, false)
					}
				})
			},

			loadFee(label, obj) {
				this.$api.get('/sysDict/list', {
					params: {
						label: label
					}
				}).then(res => {
					if (res.code && res.code != 200) {
						return
					};
					this[obj] = res.list[0].value
				})
			}
		}
	}
</script>

<style lang="scss">
	.confirm-btn {
		width: calc(100% - 60rpx);
		background: #ffa098;
		margin: 30px auto;
		height: 40px;
		line-height: 40px;
		border-radius: 50px;
		font-size: 30rpx;
		padding: 0;
		font-weight: bold;
		color: white;
		text-align: center;
		cursor: pointer;
	}

	.mybox {
		width: calc(100% - 120rpx);
		background: #fff;
		margin: 22rpx auto;
		border-radius: 14rpx;
		padding: 20rpx 30rpx;

		.pay-li {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 40rpx;

			&:last-child {
				margin: 0;
			}

			.left {
				display: flex;
				align-items: center;
				font-size: 26rpx;

				.img {
					width: 60rpx;
					height: 60rpx;
					border-radius: 100%;
					margin-right: 20rpx;

				}
			}

			.iconfont {
				font-size: 36rpx;
			}

			.iconxuanzhong {
				color: $color-orange;
			}

		}

		.input {
			border: 1px solid #efefef;
			border-radius: 10rpx;
			height: 84rpx;
			line-height: 84rpx;
			font-size: 24rpx;
			margin-bottom: 24rpx;
			padding: 0 24rpx;

			&:last-child {
				margin-bottom: 0;
			}
		}

		.title-line {
			font-size: 32rpx;
			margin-bottom: 30rpx;

			.font-small {
				color: #969696
			}
		}

		.tips-box {
			display: flex;
			justify-content: space-between;
			font-size: 26rpx;

			.tips {}

			.allin {
				color: $color-primary;
			}
		}
	}
</style>
